 @font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 200 800; /* Asegúrate de incluir los pesos que descargaste */
  src: url('../fonts/Karla-VariableFont_wght.ttf') format('ttf'); /* Ruta al archivo de fuente */
}

@font-face {
  font-family: 'Noto Sans TC';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/Buda-Light.ttf') format('opentype');
}

@import "./Colors.css";

.home {
  height: 94vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
font-family: 'Karla', sans-serif;
}

.headerContainer {
  justify-content: left;
  background-color: var(--color-black);
  width: auto;
  margin-right: 10%;
  width: 64%;
  margin: 35%;
  color: rgb(255, 255, 255);
  padding-bottom: 0.6%;
  border-radius: 10px;
}

.home.loaded {
  background-image: url('https://res.cloudinary.com/ddsqxrn0c/image/upload/v1700402970/Asia/nmwpzqlkxapgrx7j2kq9.webp'); /* Reemplaza 'ruta-de-la-imagen' con la ruta correcta de tu imagen */
  background-size: cover; /* Ajusta el tamaño de la imagen de fondo */
  background-position: center;
}

.home .headerContainer h2 {
justify-content: end;
align-items: center;
font-weight: bold;
font-size: 55px;
font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
color:rgb(255, 255, 255) ;
margin-left: 2%;
 font-family: 'Noto Sans TC';
}

.home .headerContainer h3 {
  margin-top: 15px;
  padding: 0;
  font-size: 30px;
  font-weight: lighter;
  color: rgb(255, 255, 255);
  margin-left: 2%;
font-family: 'Noto Sans TC';
}

.headerContainer button {
  background-color: var( --color-red);
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  height: 50px;
  width: 180px;
  text-decoration: none;
  font-family: 'Karla', sans-serif;
  color: rgb(255, 255, 255);
  font-size: 20px;
  margin-left: 2%;
}

.headerContainer button:hover {
  background-color: var(--color-pink);
  cursor: pointer;
  transition: 0.3s ease-in;
  margin-left: 2%;
  color: var(--color-black);
}

/* Estilos base para la tarjeta */
.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  flex: 1;
  border: 2px solid var(--color-black); /* Color amarillo chino */
  border-radius: 16px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  margin: 16px;
  max-width: 300px;
  min-width: 250px; /* Evita que las tarjetas se vuelvan demasiado estrechas en dispositivos pequeños */
}

.card h2 {
  font-size: 24px;
  margin-bottom: 8px;
  color: var(--color-red);
   font-family: 'Noto Sans TC';
}

.card p {
  font-size: 16px;
  color: #0c0c0c;
}

.card-button {
  display: inline-block;
  padding: 8px 16px;
  background-color: var(--color-red); /* Color naranja chino */
  color: rgb(255, 255, 255);
  text-decoration: none;
  border-radius: 4px;
  margin-top: 12px;
  transition: background-color 0.3s ease;
}

.card-button:hover {
  background-color: var(--color-pink);
  color: var(--color-black); /* Cambio de color al hacer hover */
}

/* Estilos responsive */
@media (max-width: 768px) {
  .cards-container {
    flex-direction: column; /* Apilar tarjetas en dispositivos móviles */
    align-items: center; /* Centrar tarjetas en dispositivos móviles */
  }
}

.carusel {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.titulo_carusel {
font-size: 1.4rem;
margin-bottom: 22px;
font-family: 'Karla', sans-serif;
color: var(--color-red);
 font-family: 'Buda Light', sans-serif;
z-index: 3;
}
.titulo_carusell {
font-size: 1rem;
margin-bottom: 24px;
font-family: 'Noto Sans TC';
color: var(--color-black);
z-index: 3;
}

@media (max-width: 600px) {
  .titulo_carousel {
    font-size: 28px;
  }
}

.keywords {
  display: flex;
  width: 100%;
  height: 100%;
  background-image: url('../images/vb.webp'); /* Reemplaza 'ruta-de-la-imagen.jpg' con la ruta correcta de tu imagen de fondo */
  background-size: cover; /* Ajusta el tamaño de la imagen de fondo */
  align-items: center;
  margin-top: 10px;
}

.keywords p {
  margin: 5px;
  padding: 10px;
  color: black;
  background-color: var(--color-pink);
  border-radius: 8px;
  text-align: left;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
}
.fondo {
  color: black;
  justify-content: center;
  background-color: var(--color-pink);
  border-radius: 8px;
  margin: 2rem;
  padding: 20px;
  align-items: center; /* Centramos verticalmente */
  border-right: 5px solid #fff; /* Borde en el lado derecho */
  border-bottom: 5px solid #fff; /* Borde en la parte inferior */
}
.fondo::after {
  position: absolute;
  bottom: -5px; /* Desplazamos la sombra hacia abajo */
  right: -5px; /* Desplazamos la sombra hacia la derecha */
  width: 100%;
  height: 5px; /* Altura de la sombra en la parte inferior */
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5); /* Sombra en el lado derecho y abajo */
}
.fondo p {
  margin: 10px 0;
  font-family: 'Laila', sans-serif;
  font-family: 'Prompt', sans-serif;
}
.fondo_center {
  font-family: 'Laila', sans-serif;
  font-family: 'Prompt', sans-serif;
  text-align: justify;
}
.fondo_center span {
  display: inline-block;
  margin-left: 10px; /* Agrega un margen izquierdo igual para todos los elementos span */
  margin-right: 10px; /* Agrega un margen derecho igual para todos los elementos span */
}
.fondo_center h2 {
  display: inline; /* Mostrar los títulos en línea */
  margin: 0; /* Eliminar el margen de los títulos */
  font-size: inherit; /* Heredar el tamaño de fuente del elemento padre */
  font-weight: normal; /* Eliminar negrita de los títulos */
}

.fondo span h2 {
  font-size: 18px;
  margin-top: 10px;
}

/* Estilos para dispositivos móviles (Responsive) */
@media (max-width: 768px) {
  .fondo {
    padding: 10px;
  }
}

.space {
  width: 100%;
  background-color: var(--color-orange);
  height: 1.4vh;
}

@media only screen and (max-width: 600px) {
  .home {
    justify-content: center;
    align-items: center;
  }
  .headerContainer {
    margin: center auto;
    border-radius: 10px;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--color-black);
    color: white;
  }
  .home .headerContainer h1 {
    font-size: 38px;
    height: 30px;
    margin: 20px 0px;
    color: white;
  }
  .home .headerContainer p {
    margin: center auto;
    font-size: 20px;
    color: white;
  }
  .headerContainer button {
    color: white;
    margin-bottom: 30px;
  }
}
