
@import "../../styles/Colors.css";
/* Estilo para el encabezado del restaurante */

  /* Estilo para el mapa */
  .map-container {
    background-color: #fff; /* Fondo blanco */
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2); /* Sombra suave */
    border-radius: 5px;
    overflow: hidden; /* Ocultar contenido que se desborde */
    margin: 20px auto; /* Margen exterior */
    max-width: 800px; /* Ancho máximo del mapa */
  }
  
  /* Estilo para evitar que el mapa se desplace antes de cargar */
  .map-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px; /* Altura mínima para evitar que el contenido salte al cargar */
    font-size: 20px;
    color: #000000; /* Color de texto de carga */
  }
  
  /* Estilo para el mapa iframe */
  .map-iframe {
    width: 100%;
    height: 300px;
    border: 0;
  }
  
  /* Estilo para el pie de página o información adicional */
  .footer {
    text-align: center;
    font-size: 14px;
    color: #666;
    padding: 10px;
  }
  