/* Importa los colores */
@import "../styles/Colors.css";

/* ... (Tus reglas CSS comunes) */

.title {
  font-size: 1.4rem;
  position: absolute;
  top: 50%; /* Centra verticalmente */
  left: 50%; /* Centra horizontalmente */
  transform: translate(-50%, -50%); /* Centra tanto vertical como horizontalmente */
  opacity: 0; /* Oculta el título por defecto */
  transition: opacity 0.3s ease; /* Agregado efecto de transición en hover */
  color: var(--color-black); /* Establece el color del texto en negro */
  background-color: var(--color-pink); /* Establece el fondo en color rosa (pink) */
  border-radius: 8px;
  padding: 8px 16px; /* Añade espaciado interno para el texto */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Agregado efecto de sombra */
}

.section-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 80%;
  margin: 0 auto;
  margin-top: 8px;
}

.section-item {
  text-align: center;
  margin: 0.5rem;
  padding: 1rem;
  box-sizing: border-box;
  background-size: cover;
  width: calc(25% - 1rem); /* Cambiado el ancho para acomodar 4 elementos en una fila */
  border-radius: 8px;
  position: relative; /* Para permitir superponer el texto en hover */
  overflow: hidden; /* Oculta el desbordamiento de las imágenes */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Agregado efecto de sombra */
}

.image-container {
  width: 100%;
  padding-top: 100%; /* Establece una relación de aspecto 1:1 para el contenedor */
  position: relative; /* Asegura que el contenido interno sea relativo a este contenedor */
}

.image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; /* Ajustado para mantener una relación de aspecto flexible */
  border-radius: 8px;
  transition: transform 0.3s ease; /* Agregado efecto de transición en hover */
  object-fit: cover; /* Añadido para controlar cómo se ajusta la imagen dentro del contenedor */
}

/* Define el ancho y el alto de las imágenes */
.image.width-200 {
  width: 200px;
}

.image.height-100 {
  height: 100px;
}

.title.mobile {
  border-radius: 0; /* Elimina el borde redondeado en dispositivos móviles */
  box-shadow: none; /* Elimina la sombra en dispositivos móviles */
}

.section-item.mobile {
  width: 90%; /* Ajusta el ancho para que los elementos ocupen el 90% del ancho del dispositivo en móviles */
  margin: 0.5rem auto;
  padding: 0.5rem;
  max-height: none;
}

/* Agrega una clase para ocultar los elementos en estado normal */
.hover-text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0; /* Oculta los elementos por defecto */
  transition: opacity 0.3s ease; /* Agrega un efecto de transición para la opacidad */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Define el estilo de los elementos en estado hover */
.section-item:hover .hover-text {
  opacity: 1; /* Muestra los elementos en estado hover */
  background-color: rgba(255, 255, 255, 0.382); /* Fondo semitransparente blanco */
  border-radius: 8px; /* Agrega bordes redondeados */
  padding: 8px; /* Espaciado interno */
   font-size: 20px;
  font-family: monospace;
  font-weight: 200;
    color: var(--color-black);
}


.description {
  color: var(--color-black);
  background-color: var(--color-pink);
  border-radius: 8px;
  padding: 8px 16px;
}

