@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 200 800; /* Asegúrate de incluir los pesos que descargaste */
  src: url('../fonts/Karla-VariableFont_wght.ttf') format('ttf'); /* Ruta al archivo de fuente */
}
.navigation-menu {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navigation-menu li {
  margin: 0px 20px;
}

.nav-bag {
  display: flex;
  align-items: center;
}

.bag-quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background: rgba(255, 230, 3, 0.972);
  font-size: 14px;
  font-weight: 700;
  color: black;
  margin-left: 5px;
}
.navigation-menu li a {
  text-decoration: none;
  color: white;
}
.navigation-menu li a:hover {
  color: #a38a0c; 
}
.active {
  color: goldenrod !important;
  font-weight: bold !important;
}
.mobile-navigation {
  list-style: none;
}
.mobile-navigation li {
  margin-top: 20px;
}

.mobile-navigation li a {
  text-decoration: none;
  font-size: 1.4rem;
  color: rgb(255, 255, 255);
}

@media (max-width: 767px) {
  .logo-link {
  }
  .logo {
    width: 150px; 
    height: 53px; 
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .logo-link {
  
  }
  .logo {
    width: 180px; 
    height: 64px;
  }
}
/* Estilos para escritorio */
