
.salon-container {
    max-width: 1114px;
    padding: 10px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    text-align: center;
    color: #333;
  }
  .salon-header {
    background: linear-gradient(#FFFFFF);
    padding: 20px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  .salon-title {
    font-size: 28px;
    color: #333; 
  }
  
  .salon-banner {
   background: linear-gradient(to bottom, #F2C99C, #FFFFFF);
    padding: 20px;
  }
  
  .banner-text-large {
    font-size: 36px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .banner-text-small {
    font-size: 20px;
    color: #555; 
    margin-bottom: 10px;
  }
  
  .menu-button {
    background-color: #F2C94C; 
    color: #333; 
    font-size: 20px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
  }
  
  .menu-button:hover {
    background-color: #E1B24A;
  }
  .salon-info {
    padding: 20px;
    text-align: left;
  }
  .salon-info p {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .salon-call-to-action {
   background: #FFD700;
   padding: 20px;
   font-weight: bold;
  
  }
  
  .salon-call-to-action p {
    font-size: 20px;
    color: #333; 
  }

  .salon-footer {
    background: linear-gradient(to bottom, #F2C99C, #FFFFFF);
    padding: 20px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }

  a {
    color: #333; 
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  