/* Estilo para la imagen */
.imagen {
  /* Ajusta la imagen para que se ajuste al ancho */
  max-width: 100%;
  height: auto;
  margin-top: 10px; /* Agrega espacio en la parte superior */
}

/* Media query para pantallas más grandes (a partir de 768px de ancho) */
@media (min-width: 768px) {
  /* Cambia el diseño a una fila y ajusta espacios en pantallas grandes */
  .container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0;
  }
  
  /* Ajusta el ancho del Tetris y la imagen en pantallas grandes */
  .tetris-wrapper {
    width: 50%;
    max-width: none;
    margin-bottom: 0;
  }
  
  .imagen {
    max-width: 45%;
    margin-top: 0;
  }
}

/* Media query para pantallas muy pequeñas (hasta 240px de ancho) */
@media (max-width: 240px) {
  /* Cambia a una columna y agrega espacio en pantallas muy pequeñas */
  .container {
    flex-direction: column;
    padding: 10px;
  }
  
  /* Ocupa todo el ancho disponible en pantallas muy pequeñas */
  .tetris-wrapper {
    width: 100%;
  }
  
  /* Ocupa todo el ancho disponible en pantallas muy pequeñas */
  .imagen {
    max-width: 100%;
  }
}

/* Estilos para pantallas más pequeñas */
@media screen and (max-width: 768px) {

  .contenedor_menu {
    font-family: 'Noto Sans TC';
  padding: 20px;
  text-align: center;
  margin: 14px;
}

  .titulo_menu {
    margin-top: 14px;
    font-size: 26px;
  }

  .titulo_seccion {
    font-size: 38px;
  }

  .nombre_plato {
    font-size: 28px;
      margin-top: 14px;
  }

  .descripcion_plato {
    font-size: 14px;
    margin-top: 14px;
  }

  .precio_plato {
    font-size: 14px;
      margin-top: -5px;
  }

  .btn-regresar {
    font-size: 14px;
  }
}


