/* Estilos base para la carta de restaurante chino */
.contenedor_cartas {
  padding: 20px;
  font-family: 'Noto Sans TC', sans-serif;
  background-color: #fffdfb86;
  text-align: center;
  margin: 0 auto; /* Centra horizontalmente */
  max-width: 1200px; /* Limita el ancho máximo */
}

.cartas-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra verticalmente */
  margin-top: 60px; /* Ajusta el margen superior */
}

.carta-title,
.menu-title {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: hsl(0, 68%, 46%);
}

.product-list {
  display: flex; /* Cambiado a flex para centrar los elementos */
  flex-wrap: wrap; /* Permite que los elementos se envuelvan en pantallas pequeñas */
  justify-content: center; /* Centra horizontalmente los elementos */
  gap: 20px; /* Espaciado entre elementos */
}

.product-item {
  flex: 1; /* Ocupa todo el ancho disponible */
  min-width: 250px; /* Ancho mínimo de cada carta */
  margin-bottom: 30px;
  border: 1px solid #d4d4d42c;
  border-radius: 8px;
  transition: transform 0.2s ease-in-out;
  background-color: #fff;
}

.product-item:hover {
  transform: scale(1.02);
}

.product-info {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 15px;
}

.product-name {
  font-size: 1.2rem; /* Reducir el tamaño del texto */
  margin-bottom: 10px;
 color: hsl(0, 2%, 40%);
}

.product-description {
  margin-bottom: 8px;
  color: #252424;
    font-size: 1.4rem; 
}

.product-price {
  font-weight: bold;
  color: hsl(0, 0%, 24%);
}

/* Estilos adicionales para dispositivos móviles */
@media (max-width: 500px) {
  .product-item {
    flex: 1 100%; /* Ocupa el 100% del ancho en pantallas pequeñas */
  }

  .product-info {
    padding: 10px;
  }

  .product-name {
    font-size: 1rem; /* Reducir el tamaño del texto */
  }
}

/* Estilos para dispositivos aún más pequeños */
@media (max-width: 380px) {
  .btn-regresare {
    width: 100%; /* Ocupa todo el ancho disponible */
    font-size: 0.8rem;
  }
    .product-name {
    font-size: 0.6rem; /* Reducir el tamaño del texto */
  }
}
