/* Estilos CSS para .figura-container */
.figura-container {
    width: 100%;
    height:100%;
    max-width: 100%; /* Asegura que no se estire más allá del tamaño original */
    max-height: 33px; /* Altura máxima de 39px */
    display: flex;
    justify-content: center; /* Centra la imagen horizontalmente */
    align-items: center; /* Centra la imagen verticalmente */
  }
  
  /* Estilos CSS para .figura-image */
  .figura-image {
    max-width: 100%; /* Asegura que la imagen no se desborde del contenedor */
    height: 100%; /* Altura automática para mantener la relación de aspecto */
  }
  