
/* Cart */


@font-face {
  font-family: 'Buda';
  font-style: normal;
  font-weight: 200 800; /* Asegúrate de incluir los pesos que descargaste */
  src: url('../fonts/Buda-Light.ttf') format('ttf'); /* Ruta al archivo de fuente */
}

.cart-container {
    padding: 0rem 1.2rem;
  }
  .cart-container h2 {
    font-weight: 400;
    font-size: 30px;
    text-align: center;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-container .titles {
    margin: 2rem 0 1rem 0;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-container .titles h3 {
    font-size: 16px;
    font-weight: 500;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item,
  .cart-container .titles {
    display: grid;
    align-items: center;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    column-gap: 0.5rem;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item {
    border-top: 1px solid rgb(187, 187, 187);
    padding: 1rem 0;
  }
  .cart-container .titles .product-title {
    padding-left: 0.5rem;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-container .titles .total {
    padding-right: 0.5rem;
    justify-self: right;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item .cart-product {
    display: flex;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item .cart-product img {
    width: 34px;
    height:34px ;
    max-width: 60%;
    margin-right: 1rem;
  }
  .cart-item .cart-product h3 {
    font-weight: 600;
     font-size: 18px;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item .cart-product button {
    border: none;
    outline: none;
    margin-top: 0.7rem;
    cursor: pointer;
    background: none;
    color: gray;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item .cart-product button:hover {
    color: black;
     font-family: 'Buda Light', sans-serif;
  }

  .cart-item .cart-product-quantity {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 130px;
    max-width: 100%;
    border: 0.5px solid rgb(177, 177, 177);
    border-radius: 5px;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item .cart-product-quantity button {
    border: none;
    outline: none;
    background: none;
    padding: 0.7rem 1.5rem;
    cursor: pointer;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-item .cart-product-quantity .count {
    padding: 0.7rem 0;
  }
  .cart-item .cart-product-total-price {
    padding-right: 0.5rem;
    justify-self: right;
    font-weight: 700;
     font-family: 'Buda Light', sans-serif;
  }
  
  /* cart summary */
  .cart-summary {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-top: 1px solid rgb(187, 187, 187);
    padding-top: 2rem;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-summary .clear-btn {
    width: 130px;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    border: 0.5px solid rgb(177, 177, 177);
    color: gray;
    background: none;
    outline: none;
    cursor: pointer;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-checkout {
    width: 270px;
    max-width: 100%;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-checkout .subtotal {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-checkout .amount {
    font-weight: 700;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-checkout p {
    font-size: 14px;
    font-weight: 200;
    margin: 0.5rem 0;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-checkout button {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    background-color: #4b70e2;
    color: #f9f9f9;
    border: none;
    outline: none;
    cursor: pointer;
     font-family: 'Buda Light', sans-serif;
  }
  .continue-shopping,
  .start-shopping {
    margin-top: 1rem;
    font-family: 'Buda Light', sans-serif;
  }
  .continue-shopping a,
  .start-shopping a {
    color: gray;
    text-decoration: none;
    display: flex;
    align-items: center;
     font-family: 'Buda Light', sans-serif;
  }
  .continue-shopping a span,
  .start-shopping a span {
    margin-left: 0.5rem;
     font-family: 'Buda Light', sans-serif;
  }
  .cart-empty {
    font-size: 20px;
    margin-top: 2rem;
    color: rgb(84, 84, 84);
    display: flex;
    flex-direction: column;
    align-items: center;
     font-family: 'Buda Light', sans-serif;
  }
  


  
@media (max-width: 665px) {
    /* Cart */
    .cart-container {
      padding: 2rem;
    }
    .cart-container .titles {
      display: none;
    }
    .cart-item,
    .cart-container .titles {
      grid-template-columns: 1fr;
      row-gap: 1rem;
    }
    .cart-item .cart-product-total-price {
      justify-self: left;
    }
    .cart-summary {
      flex-direction: column;
    }
    .cart-summary .clear-btn {
      margin-bottom: 2rem;
    }
  }
  