@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 200 800; /* Asegúrate de incluir los pesos que descargaste */
  src: url('../fonts/Karla-VariableFont_wght.ttf') format('ttf'); /* Ruta al archivo de fuente */
}

.mini-footer {
  text-align: center;
  background-color: #ffffff;

  color: rgb(54, 54, 54);
  padding: 10px;
  font-family: 'Karla', sans-serif;
}

.mini-footer a {
  text-decoration: none;
  color: rgb(51, 51, 51);
}

.mini-footer a:hover {
  cursor: default;
}

.mini-footer p {
  background-color: #fcfcfc5d;
    border-radius: 8px;
  color: rgb(29, 29, 29);
  text-align: center;
  margin: 0 auto; /* Centra horizontalmente el texto */
  padding: 5px 10px; /* Agrega espacio interno alrededor del texto */
}


