@keyframes colorChange {
  0% {
    color: red;   /* Color inicial al 0% de la animación */
  }
  50% {
    color: rgb(219, 230, 14); /* Color intermedio al 50% de la animación */
  }
  100% {
    color: rgb(255, 0, 208);  /* Color final al 100% de la animación */
  }
}

.led-title {
  display: inline-block;
  animation: colorChange 3s infinite alternate;  /* Aplicación de la animación al texto */
}
