

:root {
    /* Define variables de colores hex */
    --color-red: #D6112F;
    --color-yellow: #FCE808;
    --color-orange: #FC6603;
    --color-pink: #FCF0F2;
    --color-black: #0F0F0E;
    --color-white: #ffffff;
    /* Define variables de colores rgba */
    --color-red-rgba: rgba(214, 17, 46, 1);
    --color-yellow-rgba: rgba(252, 232, 7, 1);
    --color-orange-rgba: rgba(252, 102, 2, 1);
    --color-pink-rgba: rgba(252, 239, 241, 1);
    --color-black-rgba: rgba(15, 15, 14, 1);
  
    /* Define variables de colores hsla */
    --color-red-hsla: hsla(0, 85, 45, 1);
    --color-yellow-hsla: hsla(0, 97, 50, 1);
    --color-orange-hsla: hsla(0, 98, 49, 1);
    --color-pink-hsla: hsla(0, 71, 96, 1);
    --color-black-hsla: hsla(0, 3, 5, 1);
  }
  
  