/* CheckoutPage.css */

@font-face {
  font-family: 'Buda';
  font-style: normal;
  font-weight: 200 800; /* Asegúrate de incluir los pesos que descargaste */
  src: url('../fonts/Buda-Light.ttf') format('ttf'); /* Ruta al archivo de fuente */
}
body {

  margin: 0;
  padding: 0;
}

.cart-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  color: #D32F2F;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
   font-family: 'Buda Light', sans-serif;
}

.checkout-summary {
  margin-top: 20px;
}

.domicilios-delivery-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
  font-family: 'Buda Light', sans-serif;
 font-family: 'Buda Light', sans-serif;
  
}

label {
  display: block;
  border-radius: 4px;
  margin-bottom: 5px;
   font-family: 'Buda Light', sans-serif;
}

select,
input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  box-sizing: border-box;
   font-family: 'Buda Light', sans-serif;
}

.checkout-button {
  background-color: #4CAF50;
  border-radius: 14px;
  color: white;
  padding: 15px;
  font-size: 1.2rem;
  border: none;
  cursor: pointer;
}

.checkout-button:hover {
border-radius: 14px;
  background-color: #45a049;
}

.delivery-summary {
  margin-top: 20px;
   font-family: 'Buda Light', sans-serif;
}

.item {
  margin: 10px 0;
  font-size: 1.2rem;
  color: rgb(37, 37, 37)e1e;
   font-family: 'Buda Light', sans-serif;
}

.item span {
  font-weight: bold;
  margin-left: 10px;
  color: #131212;
   font-family: 'Buda Light', sans-serif;
}
