/* Importa los colores */
@import "./Colors.css";

/* Estilos para el contenedor de horarios */
.horarios-container {
  background-color: var(--color-white); /* Fondo rosa */
  border: 1px solid var(--color-black); /* Borde negro */
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  margin: 20px auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  transition: transform 0.3s ease; /* Efecto de transición en hover */
}

/* Cambia el cursor al pasar el ratón por encima */
.horarios-container:hover {
  transform: translateY(-5px); /* Levanta ligeramente el contenedor en hover */
}

/* Estilos para el título de horarios */
.horarios-container h2 {
  font-size: 28px;
  color: var(--color-black); /* Título en rojo */
  margin-bottom: 10px;
  font-family: 'Laila', sans-serif; /* Fuente personalizada */
}

/* Estilos para cada elemento de horario */
.horario-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 20px;
  align-items: center;
}

/* Estilos para el día de la semana */
.horario-item p:first-child {
  font-weight: bold;
  color: var(--color-black); /* Día de la semana en rojo */
}

/* Estilos para el horario */
.horario-item p:last-child {
  font-style: italic;
  color: var(--color-black); /* Horario en negro */
  font-family: 'Prompt', sans-serif; /* Fuente personalizada */
}
